@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  @apply bg-[#020617] text-white;
}


body {
  margin: 0;
  font-family: Arial, sans-serif;
  background: #0f172a;
  color: white;
}

.navbar {
  display: flex;
  justify-content: space-between;
  padding: 20px;
}

.navbar span {
  color: #22d3ee;
}

.navbar a {
  margin-left: 20px;
  color: white;
  text-decoration: none;
}

.hero {
  text-align: center;
  padding: 100px 20px;
}

.btn {
  display: inline-block;
  margin-top: 20px;
  background: #22d3ee;
  color: black;
  padding: 12px 24px;
  border-radius: 6px;
  text-decoration: none;
}

.section {
  padding: 60px 20px;
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: 20px;
}

.card {
  background: #020617;
  padding: 20px;
  border-radius: 10px;
}

.form input,
.form textarea {
  width: 100%;
  margin-bottom: 15px;
  padding: 10px;
}

footer {
  text-align: center;
  padding: 20px;
  color: #94a3b8;
}
